// src/components/WelcomeSection.js
import React, { useEffect, useLayoutEffect, useMemo, useRef } from 'react';
import {
  Box,
  Grid,
  Typography,
  Chip,
  Avatar,
  Stack,
  Link as MuiLink,
  Paper,
  Divider,
} from '@mui/material';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import LinkIcon from '@mui/icons-material/Link';
import { useLocation, useNavigate } from 'react-router-dom';
import idPicture from '../assets/id-picture.jpg';
import hfBadge from '../assets/hf.png';
import tulaneBadge from '../assets/tulane.png';
import hunterBadge from '../assets/hunter.png';
import {
  welcome as welcomeData,
  contact as contactData,
  navigation,
  theme as themeData,
} from '../data';
import { scrollElementIntoView, scrollToTop } from '../utils/scroll';
import { sharedChipSx, sharedChipProps } from '../styles/chipStyles';

const assetSources = {
  'id-picture.jpg': idPicture,
  'hf.png': hfBadge,
  'tulane.png': tulaneBadge,
  'hunter.png': hunterBadge
};

const channelIcons = {
  email: <EmailOutlinedIcon fontSize="small" />,
  linkedin: <LinkedInIcon fontSize="small" />,
  github: <GitHubIcon fontSize="small" />,
};

const SECTION_ORDER = ['about', 'education', 'experience', 'projects'];

function WelcomeSection({ heroCollapsed, onHeroCollapseChange, accentColor }) {
  const heroRef = useRef(null);
  const heroHeightRef = useRef(0);
  const navigate = useNavigate();
  const location = useLocation();

  useLayoutEffect(() => {
    if (heroRef.current && heroHeightRef.current === 0) {
      const nextHeight = heroRef.current.scrollHeight;
      if (nextHeight > 0) {
        heroHeightRef.current = nextHeight;
      }
    }
  }, []);

  useEffect(() => {
    const updateHeight = () => {
      if (!heroRef.current) {
        return;
      }
      const nextHeight = heroRef.current.scrollHeight;
      if (nextHeight > 0 && nextHeight !== heroHeightRef.current) {
        heroHeightRef.current = nextHeight;
      }
    };

    updateHeight();
    window.addEventListener('resize', updateHeight);

    return () => {
      window.removeEventListener('resize', updateHeight);
    };
  }, [heroCollapsed]);

  useEffect(() => {
    if (!onHeroCollapseChange) {
      return undefined;
    }

    if (typeof window === 'undefined') {
      return undefined;
    }

    const container = document.getElementById('main-content');

    const handleScroll = () => {
      if (!heroRef.current) {
        return;
      }

      const baseHeight = heroHeightRef.current || heroRef.current.offsetHeight || window.innerHeight;
      const collapsePoint = Math.max(baseHeight * 0.45, 240);
      const containerScroll = container ? container.scrollTop : 0;
      const windowScroll = typeof window !== 'undefined' ? window.scrollY : 0;
      const hasContainerScroll =
        container && container.scrollHeight > container.clientHeight + 8;

      const currentScroll = hasContainerScroll ? containerScroll : windowScroll;
      const collapsed = currentScroll >= collapsePoint;
      onHeroCollapseChange(collapsed);
    };

    handleScroll();

    container?.addEventListener('scroll', handleScroll, { passive: true });
    window.addEventListener('scroll', handleScroll, { passive: true });

    return () => {
      container?.removeEventListener('scroll', handleScroll);
      window.removeEventListener('scroll', handleScroll);
    };
  }, [onHeroCollapseChange]);

  const assetMap = useMemo(() => assetSources, []);
  const navLinks = useMemo(() => {
    const sorted = (navigation ?? [])
      .filter((item) => item.label?.toLowerCase() !== 'welcome')
      .map((item) => {
        if (item.type === 'hash') {
          const rawPath = item.path ?? '';
          const hash = rawPath.startsWith('#') ? rawPath : `#${rawPath}`;
          const sectionId = hash.replace('#', '');
          return {
            ...item,
            sectionId,
            hash,
          };
        }

        return {
          ...item,
          to: item.path ?? '/',
        };
      });

    return sorted.sort((a, b) => {
      const aIndex = a.sectionId ? SECTION_ORDER.indexOf(a.sectionId) : SECTION_ORDER.length;
      const bIndex = b.sectionId ? SECTION_ORDER.indexOf(b.sectionId) : SECTION_ORDER.length;
      return aIndex - bIndex;
    });
  }, []);

  const scrollToNavItem = (item) => {
    if (item.sectionId) {
      if (location.pathname !== '/') {
        navigate('/', { state: { scrollToSection: item.sectionId } });
        return;
      }

      const element = document.getElementById(item.sectionId);
      if (!scrollElementIntoView(element)) {
        scrollToTop();
      }

      return;
    }

    if (typeof item.to === 'string' && location.pathname !== item.to) {
        navigate(item.to);
    }
  };

  const arrowTarget = useMemo(
    () => navLinks.find((item) => item.sectionId),
    [navLinks]
  );

  const handleArrowClick = () => {
    if (arrowTarget) {
      scrollToNavItem(arrowTarget);
    }
  };

  const handleNavClick = (item) => {
    scrollToNavItem(item);
  };

  const isItemActive = () => false;

  const skills = welcomeData?.skills ?? [];
  const roles = welcomeData?.roles ?? [];
  const tenSeconds = Array.isArray(welcomeData?.tenSeconds) ? welcomeData.tenSeconds : [];
  const affiliations = (welcomeData?.affiliations ?? []).map((affiliation) => ({
    ...affiliation,
    src: assetMap[affiliation.badge] ?? null,
  }));

  const avatarSrc = assetMap[welcomeData?.avatar] ?? null;
  const primaryColor = themeData?.primaryColor ?? welcomeData?.highlightColor ?? 'rgb(var(--dark-cyan-rgb))';
  const greeting = welcomeData?.greeting ?? 'Hello,';
  const name = welcomeData?.name ?? '';
  const intro = welcomeData?.intro ?? '';
  const contactChannels = contactData?.channels ?? [];
  const contactIntro = contactData?.intro ?? '';
  const heroMaxHeight = '100vh';
  const primaryRole = roles.length > 0 ? roles[0] : null;
  const roleAccentColor = '#1f2937';

  return (
    <Box
      id="welcome"
      ref={heroRef}
      sx={{
        position: 'relative',
        minHeight: heroCollapsed ? 0 : '100vh',
        maxHeight: heroCollapsed ? '0px' : heroMaxHeight,
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'stretch',
        justifyContent: 'center',
        px: { xs: 3, md: 6 },
        py: heroCollapsed ? 0 : { xs: 5, md: 7 },
        // scroll snapping disabled site-wide
        opacity: heroCollapsed ? 0 : 1,
        pointerEvents: heroCollapsed ? 'none' : 'auto',
        transform: heroCollapsed ? 'translateY(-40px)' : 'none',
        transition:
          'opacity 360ms ease, transform 520ms cubic-bezier(0.22, 1, 0.36, 1), max-height 640ms cubic-bezier(0.22, 1, 0.36, 1), padding 360ms ease',
        overflow: 'hidden',
        background: 'none',
        '& > *': {
          position: 'relative',
          zIndex: 1,
        },
        '&::before': {
          content: '""',
          position: 'absolute',
          top: 0,
          left: '50%',
          transform: 'translateX(-50%)',
          width: '100vw',
          height: '100%',
          background:
            'radial-gradient(circle at top left, rgba(var(--welcome-rgb), 0.26), transparent 60%), linear-gradient(180deg, rgba(var(--welcome-rgb), 0.92) 0%, rgba(var(--welcome-rgb), 0.68) 46%, rgba(255,255,255,0.78) 82%, rgba(255,255,255,1) 100%)',
          pointerEvents: 'none',
          zIndex: 0,
        },
        '&::after': {
          content: '""',
          position: 'absolute',
          left: 0,
          right: 0,
          bottom: 0,
          height: '24%',
          background: 'linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.72) 60%, rgba(255,255,255,1) 100%)',
          pointerEvents: 'none',
          zIndex: 0,
        },
      }}
    >
      <Box
        sx={{
          position: 'relative',
          borderRadius: { xs: 4, md: 6 },
          background:
            'linear-gradient(184deg, rgba(var(--dark-cyan-rgb), 0.1) 0%, rgba(var(--mauve-rgb), 0.08) 32%, rgba(255, 255, 255, 0.98) 100%)',
          border: '1px solid rgba(var(--dark-cyan-rgb), 0.22)',
          boxShadow: '0 34px 72px rgba(85, 134, 140, 0.22)',
          p: { xs: 3.2, md: 4.4 },
          maxHeight: { md: 'none' },
          display: 'flex',
          flexDirection: 'column',
          maxWidth: 1320,
          width: '100%',
          mx: 'auto',
          transition: 'transform 520ms cubic-bezier(0.22, 1, 0.36, 1)',
          transform: heroCollapsed ? 'translateY(-32px) scale(0.94)' : 'none',
          overflow: 'hidden',
          position: 'relative',
          zIndex: 1,
          '& > *': {
            position: 'relative',
            zIndex: 1,
          },
          '&::before': {
            content: '""',
            position: 'absolute',
            inset: 0,
            background:
              'radial-gradient(circle at 24% 24%, rgba(var(--dark-cyan-rgb), 0.24), rgba(var(--dark-cyan-rgb), 0.08) 58%, transparent 75%)',
            pointerEvents: 'none',
            zIndex: 0,
          },
          '&::after': {
            content: '""',
            position: 'absolute',
            left: 0,
            right: 0,
            bottom: 0,
            height: '22%',
            background: 'linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0.75) 72%, rgba(255,255,255,1) 100%)',
            pointerEvents: 'none',
            zIndex: 0,
          },
        }}
      >
        <Grid
          container
          columnSpacing={{ xs: 0, md: 5 }}
          rowSpacing={{ xs: 4, md: 0 }}
          alignItems="stretch"
          sx={{ flexGrow: 1 }}
        >
          <Grid item xs={12} md={7}>
            <Stack spacing={{ xs: 3.5, md: 4 }}>
              <Stack spacing={1.1}>
                <Typography variant="overline" color="text.secondary" sx={{ letterSpacing: 3 }}>
                  {greeting}
                </Typography>
                <Typography
                  variant="h2"
                  component="h1"
                  sx={{
                    fontWeight: 800,
                    fontSize: { xs: '2.9rem', md: '3.6rem', lg: '4rem' },
                    lineHeight: 1.08,
                  }}
                >
                  <span style={{ color: "black" }}>{name}</span>
                </Typography>
                {intro ? (
                  <Typography
                    variant="body1"
                    sx={{
                      maxWidth: 640,
                      fontSize: { xs: '1.04rem', md: '1.12rem' },
                      lineHeight: 1.7,
                      color: '#1f2937',
                    }}
                  >
                    {intro}
                  </Typography>
                ) : null}
              </Stack>

              {contactIntro ? (
                <Typography
                  variant="body2"
                  sx={{ maxWidth: 560, color: '#1f2937' }}
                >
                  {contactIntro}
                </Typography>
              ) : null}

              {contactChannels.length > 0 && (
                <Stack spacing={1.4}>
                  <Typography
                    variant="overline"
                    sx={{
                      letterSpacing: 3,
                      color: '#1f2937',
                      fontWeight: 700,
                      fontSize: { xs: '0.9rem', md: '1rem' },
                    }}
                  >
                    Quick Links
                  </Typography>
                  <Stack direction="row" spacing={1.2} alignItems="center" sx={{ flexWrap: 'wrap' }}>
                    {contactChannels.map((channel) => (
                      <MuiLink
                        key={channel.label}
                        href={channel.href}
                        target={channel.href?.startsWith('http') ? '_blank' : undefined}
                        rel={channel.href?.startsWith('http') ? 'noreferrer' : undefined}
                        aria-label={channel.label}
                        sx={{
                          color: '#1f2937',
                          display: 'inline-flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          width: 48,
                          height: 48,
                          borderRadius: '50%',
                          background:
                            'linear-gradient(140deg, rgba(var(--dark-cyan-rgb), 0.14) 0%, rgba(var(--dark-cyan-rgb), 0.2) 100%)',
                          border: '1px solid rgba(var(--dark-cyan-rgb), 0.22)',
                          boxShadow: '0 12px 24px rgba(85, 134, 140, 0.18)',
                          transition: 'transform 200ms ease, box-shadow 200ms ease, filter 180ms ease',
                          '&:hover': {
                            transform: 'translateY(-3px) scale(1.05)',
                            boxShadow: '0 18px 30px rgba(85, 134, 140, 0.24)',
                            filter: 'brightness(1.05)',
                          },
                        }}
                      >
                        {channelIcons[channel.label?.toLowerCase()] ?? <LinkIcon fontSize="small" />}
                      </MuiLink>
                    ))}
                  </Stack>
                </Stack>
              )}

              {tenSeconds.length > 0 && (
                <Stack spacing={{ xs: 1.6, md: 2 }} sx={{ maxWidth: 720 }}>
                  <Typography
                    variant="overline"
                    sx={{
                      letterSpacing: 3,
                      color: '#1f2937',
                      fontWeight: 700,
                      fontSize: { xs: '0.9rem', md: '1rem' },
                    }}
                  >
                    Me in 10 seconds
                  </Typography>
                  <Typography
                    variant="body1"
                    sx={{
                      fontWeight: 200,
                      lineHeight: { xs: 1.6, md: 1.65 },
                      fontSize: { xs: '1.3rem', md: '1.45rem' },
                      color: '#1f2937',
                    }}
                  >
                    {tenSeconds.join(' ')}
                  </Typography>
                </Stack>
              )}

            </Stack>
          </Grid>

          {/* Avatar card */}
          <Grid
            item
            xs={12}
            md={5}
            sx={{
              display: 'flex',
              justifyContent: { xs: 'center', md: 'flex-end' },
              alignItems: { xs: 'center', md: 'stretch' },
            }}
          >
            <Paper
              elevation={0}
              sx={{
                position: 'relative',
                borderRadius: 5,
                px: { xs: 2.4, sm: 2.8, md: 3.4 },
                py: { xs: 2.6, sm: 3, md: 4 },
                background:
                  'linear-gradient(170deg, rgba(var(--dark-cyan-rgb), 0.18) 0%, rgba(255,255,255,0.96) 68%)',
                border: '1px solid rgba(var(--dark-cyan-rgb), 0.24)',
                boxShadow: '0 24px 52px rgba(85, 134, 140, 0.18)',
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column',
                width: { xs: '100%', sm: 'min(360px, 100%)', md: 'min(320px, 100%)' },
                maxWidth: { xs: '100%', md: 'min(320px, 100%)' },
                mr: { md: 0 },
                ml: { md: 'auto' },
                minWidth: 0,
                height: { md: '100%' },
                overflowY: 'auto',
              }}
            >
              <Stack
                spacing={{ xs: 2.6, md: 3.2 }}
                alignItems="center"
                sx={{ width: '100%', justifyContent: 'flex-start', flexGrow: 1, minHeight: 0 }}
              >
                {/* Profile picture */}
                <Box
                  sx={{
                    position: 'relative',
                    width: { xs: 'min(200px, 58vw)', sm: 'min(220px, 52vw)', md: 240, lg: 260 },
                    height: { xs: 'min(200px, 58vw)', sm: 'min(220px, 52vw)', md: 240, lg: 260 },
                    borderRadius: '50%',
                    background:
                      'radial-gradient(circle at 28% 30%, rgba(var(--welcome-rgb), 0.3), rgba(var(--welcome-rgb), 0.06) 55%, transparent 75%)',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    border: '1px solid rgba(var(--welcome-rgb), 0.28)',
                    boxShadow: '0 18px 36px rgba(85, 134, 140, 0.22)',
                    '&::after': {
                      content: '""',
                      position: 'absolute',
                      inset: 14,
                      borderRadius: '50%',
                      border: '1px dashed rgba(var(--dark-cyan-rgb), 0.24)',
                      pointerEvents: 'none',
                    },
                  }}
                >
                  <Avatar
                    alt={name || 'Profile'}
                    src={avatarSrc ?? undefined}
                    sx={{
                      width: { xs: 'calc(100% - 32px)', md: 210, lg: 220 },
                      height: { xs: 'calc(100% - 32px)', md: 210, lg: 220 },
                    }}
                  />
                </Box>
                
                {/* Primary role */}
                {primaryRole ? (
                  <Typography
                    variant="subtitle1"
                    sx={{
                      fontWeight: 700,
                      textAlign: 'center',
                      color: roleAccentColor,
                      textShadow: '0 12px 26px rgba(0,0,0,0.16)',
                      mt: { xs: 1.6, md: 1.8 },
                    }}
                  >
                    {primaryRole}
                  </Typography>
                ) : null}

                {/* Affiliations */}
                {affiliations.length > 0 && (
                  <Stack spacing={{ xs: 1.2, md: 1.4 }} sx={{ width: '100%' }}>
                    <Divider flexItem sx={{ borderColor: 'rgba(var(--dark-cyan-rgb), 0.18)' }} />
                    <Stack direction="row" spacing={1.25} justifyContent="center" flexWrap="wrap">
                      {affiliations.map((affiliation) => (
                        <Avatar
                          key={affiliation.name}
                          alt={affiliation.name}
                          src={affiliation.src ?? undefined}
                          sx={{ width: 44, height: 44 }}
                        />
                      ))}
                    </Stack>
                  </Stack>
                )}

                {/* Core Skills */}
                {skills.length > 0 && (
                  <Stack spacing={{ xs: 1.6, md: 1.8 }} sx={{ width: '100%' }}>
                    <Typography
                      variant="overline"
                      sx={{
                        letterSpacing: 3,
                        color: '#1f2937',
                        fontWeight: 700,
                        textAlign: 'center',
                        fontSize: { xs: '0.9rem', md: '1rem' },
                      }}
                    >
                      Core Skills
                    </Typography>
                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, justifyContent: 'center' }}>
                      {skills.map((skill) => (
                        <Chip
                          key={skill}
                          label={skill}
                          {...sharedChipProps}
                          sx={{
                            ...sharedChipSx,
                            color: '#1f2937',
                            borderColor: 'rgba(31,41,55,0.22)',
                          }}
                        />
                      ))}
                    </Box>
                  </Stack>
                )}

              </Stack>
            </Paper>
          </Grid>
        </Grid>

        <Box sx={{ mt: { xs: 3, md: 3.5 } }} />
      </Box>

      <Box
        onClick={handleArrowClick}
        sx={{
          position: 'fixed',
          bottom: { xs: 32, md: 13 },
          left: '50%',
          transform: 'translateX(-50%)',
          display: 'flex',
          alignItems: 'center',
          gap: 0.75,
          color: primaryColor,
          cursor: 'pointer',
          opacity: heroCollapsed ? 0 : 1,
          pointerEvents: heroCollapsed ? 'none' : 'auto',
          transition: 'opacity 260ms ease',
          zIndex: 10,
        }}
        role="button"
        aria-label="Scroll to next section"
      >
        <Typography variant="body2" sx={{ letterSpacing: 1.2, textTransform: 'uppercase', fontWeight: 700 }}>
          Scroll
        </Typography>
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            animation: heroCollapsed ? 'none' : 'heroArrowPulse 1.6s ease-in-out infinite alternate',
            '@keyframes heroArrowPulse': {
              from: { transform: 'translateY(0)' },
              to: { transform: 'translateY(10px)' },
            },
          }}
        >
          <KeyboardArrowDownIcon fontSize="large" />
        </Box>
      </Box>
    </Box>
  );
}

export default WelcomeSection;
